import React, {useState} from "react";


export default function TextForm(props) {

    // Convert To Uppercase
    const handelUpClick = ()=> {
        let NewText = text.toUpperCase();
        setText(NewText)
        props.showAlert("Converted To UpperCase!", "success")
    }

    // Convert To Lowercase
    const handelloClick = ()=> {
        let NewText = text.toLowerCase();
        setText(NewText)
        props.showAlert("Converted To LowerCase!", "success")
    }
    // Clear
    const handelClearClick = ()=> {
        let NewText = '';
        setText(NewText)
        props.showAlert("Text Clear!", "success")
    }

    // Copy Text
    const handleCopy  = ()=> {
        navigator.clipboard.writeText(text);
        props.showAlert("Copied to Clipboard!", "success")
    }

    // Remove Extra Space
    const handleExtraSpaces = ()=> {
        let newText = text.split(/[ ]+/);
        setText(newText.join(" "))
        props.showAlert("Extra Space Removed!", "success")
    }
    const handleOnChange = (event)=> {
        setText(event.target.value);
    }
    const [text, setText] = useState('');
  return (
      <>
    <div className="container">
        <h1>{props.heading}</h1>
      <div className="mb-3">
        <textarea className="form-control" value={text} onChange={handleOnChange}  id="myBox" rows="8"></textarea>
        <button disabled={text.length===0} className="btn btn-primary my-3 mx-2" onClick = {handelUpClick} > 
          Convert To Uppercase
        </button>
        <button disabled={text.length===0} className="btn btn-primary my-3 mx-2" onClick = {handelloClick} > 
          Convert To Lowercase
        </button>
        <button disabled={text.length===0} className="btn btn-primary my-3 mx-2" onClick = {handleCopy} > 
          Copy Text
        </button>
        <button disabled={text.length===0} className="btn btn-primary my-3 mx-2" onClick = {handleExtraSpaces} > 
          Remove Extra Space
        </button>
        <button disabled={text.length===0} className="btn btn-primary my-3 mx-2" onClick = {handelClearClick} > 
          Clear
        </button>
      </div>
    </div>
    <div className="my-3">
        <h1>Your text Summery</h1>
        <p> {text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words and {text.length} characters</p>
        <p> {0.008 * text.split(" ").filter((element)=>{return element.length!==0}).length} Minute To Read </p>
    </div>
    <div className="my-3">
        <h1>Preview</h1>
        <p>{text.length>0?text:"Nothing To Preview"}</p>
    </div>
    </>
  );
}